.small-select-height {
	.Select-menu {
		max-height: 137px !important; } }

.bootstrap-tagsinput {
	display: block;
	border: 1px solid rgb(204, 204, 204);
	border-radius: 0;
	width: 100%;
	line-height: 30px;
	.tag {
		position: relative;
		padding: 5px 22px 5px 7px;
		background: $blue;
		[data-role=remove] {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			width: 20px;
			&:after,
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				margin-left: -5px;
				border-top: 1px solid $white;
				width: 10px; }
			&:before {
				transform: rotate(-45deg); }
			&:after {
				transform: rotate(45deg); } } }

	input {
		@include f(16px, 400, normal); } }
