.form-group {
	position: relative;
	margin: 0 -10px;
	@include clr;
	&:not(:last-child) {
		// margin-bottom: 15px
		margin-bottom: 10px; }
	&_simple {
		margin-left: 0;
		margin-right: 0; }
	&_static {
		.form-group {
			&__label,
			&__value {
				padding-top: 0;
				padding-bottom: 0;
				height: auto; } } }
	&_full {
		.form-group {
			&__label,
			&__field {
				width: 100%; }
			&__label {
				text-align: left; } } }
	&_switch,
	&_multi {
		.form-group {
			&__label {
				width: 40%;
				@include sm {
					width: 100%; } }
			&__field,
			&__value {
				width: 60%;
				@include sm {
					width: 100%; } } } }
	&_columns {
		.form-group {
			&__label,
			&__field,
			&__value {
				width: 50%;
				@include sm {
					width: 100%; } } } }
	&__label,
	&__field, {
		float: left;
		padding-left: 10px;
		padding-right: 10px; }
	&__label {
		width: 30%;
		height: 40px;
		color: lighten($darkGray, 30%);
		@include vertical;
		text-align: right;
		@include sm {
			width: 100%;
			height: auto;
			text-align: left;
			margin-bottom: 5px; }
		span {
			display: inline-block;
			vertical-align: middle;
			@include sm {
				vertical-align: top; } } }
	&__switcher {
		float: left;
		padding: 5px 20px 5px 0;
		&_absolute {
			@include md {
				width: 220px; } } }
	&__input-wrapper {
		overflow: hidden;
		max-width: 90px; }
	&__field {
		width: 70%;
		@include sm {
			width: 100%; } }
	&__label span,
	&__value {
		@include f(16px, 400, 20px); }
	&__value {
		span {
			margin-left: 10px;
			opacity: 0.5; } }
	&__value {
		padding-top: 10px;
		padding-bottom: 10px;
		@include sm {
			padding-bottom: 0; } }
	&__row {
		margin: 0 -10px;
		@include clr;
		@include sm {
			padding-top: 5px; } }
	&__col {
		float: left;
		padding: 5px 10px;
		width: 50%;
		@include xs {
			width: 100%; }
		&_sm {
			width: 40%; }
		&_md {
			width: 60%; }
		&_wide {
			width: 100%; }
		.form-group {
			&__label,
			&__value,
			&__field {
				padding-left: 0;
				padding-right: 0;
				width: 100%;
				text-align: left; } } }
	&__btn {
		position: absolute;
		right: 10px;
		bottom: 0;
		width: 120px;
		height: 40px;
		font-size: 16px;
		line-height: 40px;
		~ .form-group__field {
			padding-right: 140px; } } }
