.form {
	// padding: 20px 40px
	padding: 10px 40px;
	@include sm {
		padding: 20px; }
	&_default {
		padding: 10px 40px;
		@include md {
			padding: 20px; } }
	&__submit {
		font-size: 0;
		text-align: center;
		&_multiple {
			padding-left: 70px;
			@include xs {
				padding-left: 0; } } }
	&__btn {
		max-width: 200px;
		&:not(:last-child) {
			margin-right: 20px; }
		&-group {
			display: inline-block;
			vertical-align: top;
			padding: 0 10px;
			width: 50%;
			max-width: 200px;
			@include xs {
				padding: 10px 0;
				width: 100%; } } }
	&__description {
		padding-top: 20px;
		color: lighten($darkGray, 30%);
		font-size: 12px;
		line-height: 1;
		text-align: center; }
	&__text {
		padding-bottom: 20px;
		color: lighten($darkGray, 30%);
		font-size: 14px;
		line-height: 1.2; }
	&__row {
		padding-top: 10px;
		margin: 0 auto;
		max-width: 420px;
		font-size: 0;
		text-align: center;
		@include xs {
			margin: 0 -10px;
			max-width: none; } }
	&__col {
		display: inline-block;
		vertical-align: top;
		padding: 0 10px;
		width: 50%; }
	&__footer {
		// padding-top: 30px
		padding-top: 10px; } }
