.v-settings {
	margin-top: 30px;
	&__section {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__title {
		display: block;
		padding-bottom: 20px;
		color: inherit;
		font-size: 16px;
		font-weight: 700;
		transition: color 0.25s;
		@include hover {
			color: $blue; }
		@include hover('metro') {
			color: $metroSecondary; }
		&.is-active {
			.v-settings__arrow {
				transform: rotate(180deg); } } }
	&__arrow {
		margin-left: 0.5%;
		fill: currentColor;
		font-size: 50%;
		transition: transform 0.25s; }
	&__collapse {
		display: block; }
	&__item {
		padding: 5px 0;
		margin: 0 -5px;
		font-size: 0;
		&_top {
			.v-settings {
				&__label,
				&__value {
					vertical-align: top; }
				&__label {
					padding-top: 17px; } } } }
	&__label,
	&__value {
		display: inline-block;
		vertical-align: middle;
		padding: 5px; }
	&__label {
		width: 200px;
		font-size: 16px;
		@include sm {
			width: 140px;
			font-size: 14px; } }
	&__value {
		position: relative;
		width: calc(100% - 200px);
		max-width: 450px;
		@include sm {
			width: calc(100% - 140px); } }
	&__main {
		padding-top: 20px;
		@include clr; }
	&__sidebar {
		float: left;
		width: 300px;
		@include md {
			display: none; }
		~ .v-settings__content {
			margin-left: 320px;
			@include md {
				margin-left: 0; } } }
	&__search {
		max-width: none; }
	&__footer {
		margin: 0 -10px;
		font-size: 0;
		text-align: center; }
	&__btn {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		width: 50%;
		max-width: 200px; }
	&__description {
		clear: both;
		padding: 5px 5px 10px;
		color: $blue;
		@include f(14px, 400, 1.3);
		@include metro {
			color: $metroSecondary; } }
	&__error-message {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		margin: -18px 5px 0; }
	&__content {
		position: relative; }
	&__loader {
		position: absolute;
		z-index: 100;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		background: rgba($white, 0.7);
		@include vertical;
		transition: opacity 0.25s, visibility 0.25s;
		&.is-active {
			opacity: 1;
			visibility: visible; }
		&-in {
			display: inline-block;
			vertical-align: middle;
			margin: 0 2px;
			border-radius: 50%;
			width: 7px;
			height: 7px;
			background: $darkGray;
			animation: loader 1s linear infinite;
			&:nth-child(2) {
				animation-delay: 0.25s; }
			&:nth-child(3) {
				animation-delay: 0.5s; } } } }

@keyframes loader {
	0%, 100% {
		opacity: 1; }
	// 33%
	// 	opacity: 0.48
	66% {
		opacity: 0.24; } }
