.btn-toggle {
	width: 40px;
	height: 40px;
	background: none;
	@include vertical;
	@include hover {
		.btn-toggle {
			&__visible,
			&__hidden {
				fill: $blue; } } }
	// +hover('boost')
	// 	.btn-toggle
	// 		&__visible,
	// 		&__hidden
	// 			fill: $elevatePrimary
	@include hover('metro') {
		.btn-toggle {
			&__visible,
			&__hidden {
				fill: $metroSecondary; } } }
	&.is-active {
		.btn-toggle {
			&__visible {
				display: none; }
			&__hidden {
				display: inline-block; } } }
	&__visible,
	&__hidden {
		display: inline-block;
		vertical-align: middle;
		fill: $black;
		font-size: (20px*0.75);
		transition: fill 0.25s;
		pointer-events: none; }
	&__hidden {
		display: none; } }
