body {
  position: relative;
  padding-top: 60px;
  padding-bottom: 40px;
  min-width: 320px;
  color: #333;
  background: #fff;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body,
#root {
  height: 100%;
}

.container {
  overflow: auto;
  height: 100%;
}

[hidden] {
  display: none !important;
}

.pre-line {
  white-space: pre-line;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 18px;
  font-weight: 300;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.is-metro .btn {
  background: #9784ff;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.is-metro .btn:active {
  background: #6c51ff;
}

.btn:hover .icon-right {
  transform: translate3d(50%, 0, 0);
}

.btn:hover .icon-left {
  transform: translate3d(-50%, 0, 0);
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn_gray {
  background: #bec1cb;
}

.is-metro .btn_gray {
  background: #bec1cb;
}

.btn_gray:active {
  background: #a2a6b4;
}

.is-boost .btn_gray:active {
  background: #a2a6b4;
}

.is-metro .btn_gray:active {
  background: #a2a6b4;
}

.btn_red {
  background: #ec4a41;
}

.is-metro .btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.is-metro .btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn .icon-right,
.btn .icon-left {
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
  font-size: 80%;
  margin-top: 22px;
  transition: transform .175s;
}

.btn-icon {
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: #333;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.25s, box-shadow 0.25s;
}

.btn-icon:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .btn-icon:hover {
  background: rgba(0, 0, 0, 0.05);
}

.btn-icon:active {
  background: rgba(0, 0, 0, 0.1) !important;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2) !important;
}

.btn-icon:disabled, .btn-icon.is-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn-icon_red {
  color: #ec4a41;
}

.btn-icon__icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 30px;
}

.btn-toggle {
  width: 40px;
  height: 40px;
  background: none;
  text-align: center;
  font-size: 0;
}

.btn-toggle:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .btn-toggle:hover .btn-toggle__visible, .no-touch .btn-toggle:hover .btn-toggle__hidden {
  fill: #2e99db;
}

.no-touch.is-metro .btn-toggle:hover .btn-toggle__visible, .no-touch.is-metro .btn-toggle:hover .btn-toggle__hidden {
  fill: #9784ff;
}

.btn-toggle.is-active .btn-toggle__visible {
  display: none;
}

.btn-toggle.is-active .btn-toggle__hidden {
  display: inline-block;
}

.btn-toggle__visible, .btn-toggle__hidden {
  display: inline-block;
  vertical-align: middle;
  fill: #000;
  font-size: 15px;
  transition: fill 0.25s;
  pointer-events: none;
}

.btn-toggle__hidden {
  display: none;
}

